<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="6">
        <el-input type="text" placeholder="" v-model="accountName" clearable></el-input>
      </el-col>
      <el-col :span="6">
        <el-input type="text" placeholder="" v-model="name" clearable></el-input>
      </el-col>
      <el-col :span="6">
        <el-input type="text" placeholder="" v-model="telphone" clearable></el-input>
      </el-col>
      <el-col :span="1">
        <el-button icon="el-icon-search" circle @click="search"></el-button>
      </el-col>
      <el-col :span="3">
        <el-button icon="el-icon-plus" circle @click="add"></el-button>
        <el-dialog
          title=""
          id="modify"
          :visible.sync="addDialog"
          center
          style="top:-120px"
          :close-on-click-modal="false"
        >
          <el-form :model="formAdd" ref="formAddRule" :rules="rules">
            <el-form-item label="" prop="accountName">
              <el-input v-model="formAdd.accountName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="password">
              <el-input v-model="formAdd.password"></el-input>
            </el-form-item>
            <el-form-item label="" prop="name">
              <el-input v-model="formAdd.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="idNumber">
              <el-input v-model="formAdd.idNumber"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-radio-group v-model="formAdd.gender">
                <el-radio label=""></el-radio>
                <el-radio label=""></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="">
              <el-col :span="11">
                <el-date-picker
                  type="date"
                  placeholder=""
                  v-model="formAdd.birth"
                  style="width: 100%;"
                ></el-date-picker>
              </el-col>
            </el-form-item>
            <el-form-item label="" prop="telphone">
              <el-input v-model.number="formAdd.telphone"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="cancleAdd"> </el-button>
            <el-button type="primary" @click="confirmAdd('formAddRule')"> </el-button>
          </div>
        </el-dialog>
      </el-col>
    </el-row>
    <el-row>
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        v-loading="loading"
        v-bind:element-loading-text="loadingText"
        element-loading-background="rgba(0, 0, 0, 0.8)"
        :default-sort="{prop:'accountId',order:'ascending'}"
      >
        <el-table-column prop="accountId" label="id" align="center" width="150" sortable></el-table-column>
        <el-table-column prop="accountName" label="" align="center" width="200"></el-table-column>
        <el-table-column prop="name" label="" align="center" width="200"></el-table-column>
        <el-table-column prop="gender" label="" align="center" width="100"></el-table-column>
        <el-table-column prop="birth" label="" align="center" width="200"></el-table-column>
        <el-table-column prop="telephone" label="" align="center" width="200"></el-table-column>
        <el-table-column prop="remainderMoney" label="" align="center" width="150"></el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-popover placement="left" title="" trigger="click">
              <el-input v-model="idNumber" disabled></el-input>
              <el-button
                size="mini"
                slot="reference"
                @click="showIdNumber(scope.$index, scope.row)"
                icon="el-icon-info"
                type="info"
              ></el-button>
            </el-popover>&nbsp;
            <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)"
              icon="el-icon-edit"
            ></el-button>
            <el-dialog
              title=""
              id="modify"
              :visible.sync="dialogFormVisible"
              center
              style="top:-120px"
              :close-on-click-modal="false"
            >
              <el-form :model="form">
                <el-form-item label="id">
                  <el-input v-model="form.accountId" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="form.accountName" clearable></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="form.password" clearable></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="form.name" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-radio-group v-model="form.gender">
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                  </el-radio-group>
                </el-form-item>
                <el-form-item label="">
                  <el-col :span="11">
                    <el-date-picker
                      type="date"
                      placeholder=""
                      v-model="form.birth"
                      style="width: 100%;"
                      clearable
                    ></el-date-picker>
                  </el-col>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="form.telphone" clearable></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="form.remainderMoney" disabled></el-input>
                </el-form-item>
                <el-form-item label="" hidden>
                  <el-input v-model="form.status"></el-input>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="cancleModify"> </el-button>
                <el-button type="primary" @click="confirmModify"> </el-button>
              </div>
            </el-dialog>&nbsp;
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row,tableData)"
              icon="el-icon-delete"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[5, 10]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        v-bind:total="total"
      ></el-pagination>
    </el-row>
  </div>
</template>

<script>
import tableRender from "../../../api/tableRender.js";
export default {
  name: "manager",
  data() {
    return {
      tableData: [
        {
          accountId: "",
          accountName: "",
          name: "",
          gender: "",
          birth: "",
          telephone: "",
          remainderMoney: ""
        }
      ],
      currentPage: 1,
      total: 0,
      pageSize: "",
      loading: true,
      loadingText: "",
      accountName: "",
      name: "",
      telphone: "",
      idNumber: "",
      form: {
        accountId: "",
        accountName: "",
        name: "",
        gender: "",
        birth: "",
        telphone: "",
        remainderMoney: "",
        password: ""
      },
      formAdd: {
        accountId: "",
        accountName: "",
        name: "",
        gender: "",
        birth: "",
        telphone: "",
        remainderMoney: "",
        password: ""
      },
      dialogFormVisible: false,
      addDialog: false,
      rules: {
        accountName: [
          { required: true, message: "", trigger: "blur" }
        ],
        password: [{ required: true, message: "", trigger: "blur" }],
        name: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          {
            min: 2,
            max: 15,
            message: "2-15",
            trigger: "blur"
          }
        ],
        idNumber: [
          { required: true, message: "", trigger: "blur" },
          {
            pattern: /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/,
            message: "",
            trigger: "blur"
          }
        ],
        telphone: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          {
            type:"number",
            message:'',
            trigger:'blur'
          }
        ]
      }
    };
  },
  methods: {
    customerTable() {
      let that = this;
      tableRender.findAllUsers(1, 5, 1, null, null, null).then(function(res) {
        var data = res.data.data;
        var total = res.data.count;
        window.console.log("total==>", total);
        window.console.log(data);
        that.tableData = data;
        that.total = total;
        that.idNumber = res.data.data.idNumber;
        that.pageSize = 5;
        that.loading = false;
      });
    },
    handleCurrentChange(val) {
      let that = this;
      that.loading = true;
      window.console.log("", val);
      window.console.log(":", that.pageSize);
      tableRender
        .findAllUsers(1, that.pageSize, val, null, null, null)
        .then(function(res) {
          var data = res.data.data;
          var total = res.data.count;
          that.tableData = data;
          that.total = total;
          that.currentPage = val;
          that.loading = false;
        });
    },
    handleSizeChange(pageSize) {
      window.console.log(pageSize);
      let that = this;
      that.loading = true;
      tableRender
        .findAllUsers(1, pageSize, 1, null, null, null)
        .then(function(res) {
          var data = res.data.data;
          var total = res.data.count;
          that.tableData = data;
          that.total = total;
          that.pageSize = pageSize;
          that.loading = false;
        });
    },
    search() {
      let that = this;
      that.loading = true;
      tableRender
        .findAllUsers(
          1,
          that.pageSize,
          1,
          this.accountName,
          this.name,
          this.telphone
        )
        .then(function(res) {
          var data = res.data.data;
          var total = res.data.count;
          that.tableData = data;
          that.total = total;
          that.loading = false;
        });
    },
    add() {
      window.console.log("add");
      this.addDialog = true;
    },
    confirmAdd(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          window.console.log("");
          let that = this;
          tableRender.addUser(this.formAdd, "3").then(function(res) {
            window.console.log("res==>", res);
            that.loadingText=""
            that.customerTable();
            if (res.data > 0) {
              window.console.log("");
              that.$message({
                message: "",
                type: "success"
              });
              that.addDialog = false;
            } else {
              that.$message.error('');
            }
          });
        } else {
          return false;
        }
      });
    },
    cancleAdd() {
      window.console.log("");
      this.addDialog = false;
    },
    showIdNumber(index, row) {
      let that = this;
      that.idNumber = row.idNumber;
    },
    handleEdit(index, row) {
      window.console.log("index==>", index);
      window.console.log("row==>", row);
      this.dialogFormVisible = true;
      this.form.accountId = row.accountId;
      this.form.accountName = row.accountName;
      this.form.birth = row.birth;
      this.form.gender = row.gender;
      this.form.idNumber = row.idNumber;
      this.form.name = row.name;
      this.form.remainderMoney = row.remainderMoney;
      this.form.telphone = row.telephone;
      this.form.password = row.password;
      this.form.birth = row.birth;
    },
    confirmModify() {
      window.console.log("");
      let that = this;
      tableRender.modifyUsers(this.form).then(function() {
        that.loading = true;
        that.loadingText=""
        that.handleCurrentChange(that.currentPage);
        that.dialogFormVisible = false;
      });
      that.$message({
        message: "",
        type: "success"
      });
    },
    cancleModify() {
      window.console.log("");
      this.dialogFormVisible = false;
    },
    handleDelete(index, row,rows) {
      window.console.log("index==>", index);
      window.console.log("row==>", row);
      let that = this;
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(function() {
          that.form.accountId = row.accountId;
          that.form.status = 1;
          that.form.accountName = row.accountName;
          that.form.gender = row.gender;
          that.form.birth = row.birth;
          that.form.telphone = row.telphone;
          that.form.remainderMoney = row.remainderMoney;
          that.form.password = row.password;
          that.form.name = row.name;
          window.console.log(that.form);
          tableRender.modifyUsers(that.form).then(function() {
            rows.splice(index,1);
          });
          that.$message({
            type: "success",
            message: ""
          });
        })
        .catch(function() {
          that.$message({
            type: "info",
            message: ""
          });
        });
    }
  },
  mounted() {
    this.customerTable();
  }
};
</script>

<style>
</style>
